@import url('https://fonts.googleapis.com/css?family=Nunito:regular,semibold,bold,italic&subset=latin,latin-ext');
@import url('https://fonts.googleapis.com/css?family=Baskervville|Handlee|Source+Code+Pro&display=swap');


:root {
    --main-black: #222222;
    --main-blue: rgb(192, 212, 236);
    --darker-blue: rgb(114, 162, 221);
    --main-light-black: #777777;
    --light-outline: #E8E8E8;
}
body {
    margin: 0;
    font-family: Nunito, Arial, Helvetica;
    color: var(--main-black);
    padding: 0 80px;
}
.container {
    width: 75%;
    margin: 0 auto;
}
a {
    text-decoration: none;
    font-weight: 600;
    color: var(--darker-blue);
}
h1 {
    font-size: 24px;
    font-weight: 500;
}
p {
    font-size: 16px;
    line-height: 24px;
}
img {
    width: 424px;
    
}
.overlay {
    margin: 0 auto;
    background-color: var(--main-blue);
    width: 424px;
    height: 254px;
    border-radius: 8px;
}
.overlay.blue {
    background-color: #89cedd;
}
.overlay.purple {
    background-color: #9ebdf7;
}
.overlay.green {
    background-color: #4ccdac;
}
.overlay.yellow {
    background-color: #f9c986;
}
a img {
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}
a:hover img {
    opacity: 0.5;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}
.small-text {
    margin-top: 4px;
    text-align: center;
    font-size: 14px;
    color: var(--main-light-black);
}
.subtitle {
    text-align: center;
    width: 80%;
    margin: 0 auto 24px auto;
}
.btn-big {
    display: inline-block;
    margin: 16px 0;
    padding: 12px 24px;
    background-color: var(--darker-blue);
    color: white;
    border: 2px solid transparent;
    border-radius: 100px;
    transition: all .2s ease;
    -webkit-transition: all .2s ease;
}
.btn-big:hover {
    border: 2px solid var(--darker-blue);
    background-color: white;
    color: var(--darker-blue);
    transition: all .2s ease;
    -webkit-transition: all .2s ease;
}


/* -----header/nav----*/
nav {
    display: inline-block;
    width: 100%;
    margin-bottom: 32px;
}
nav a {
    color: var(--main-black);
    font-weight: 400;
    text-decoration: none;
}
.name {
    font-weight: 500;
    font-size: 20px;
    line-height: 104px;
}
nav ul {
    float: right;
    list-style: none;
}
nav ul li {
    display: inline-block;
    margin-left: 32px;
    font-size: 20px;
    font-weight: 400;
    line-height: 56px;
}
.is-active {
    border-bottom: 2px solid var(--main-blue);
    transition: all .1s ease;
    -webkit-transition: all .1s ease;
}
.underline {
  display: inline;
  position: relative;
  overflow: hidden;
}
.underline:after {
  content: "";
  position: absolute;
  z-index: -1;
  right: 0;
  width: 0;
  bottom: -2px;
  background: var(--main-blue);
  height: 2px;
  transition-property: width;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}
.underline:hover:after,
.underline:focus:after,
.underline:active:after {
  left: 0;
  right: auto;
  width: 100%;
}

/* -----about----*/
.outline {
    border: 4px solid var(--main-blue);
    border-radius: 12px;
    padding: 0px 24px;
    margin: 0 auto 0 auto;
    width: 70%;
    font-size: 20px;
    line-height: 30px;
}

/* -----Projects----*/
.introduction {
    text-align: center;
    margin-top: 16px;
    margin-bottom: 80px;
}
.introduction h1 {
    margin-top: 0px;
    margin-bottom: 24px;
    font-size: 40px;
}
.introduction p {
    font-size: 18px;
}
.introduction img {
    display: inline-block;
    height: 32px;
    width: 32px;
    margin: 14px 24px 0 24px;
}
.flex-container {
    justify-content: center;
    display: flex;
}
.designer-font {
    font-family: Baskerville;
    font-size: 22px;
}
.programmer-font {
    font-family: "Source Code Pro";
}
.writer-font {
    font-family: "Handlee";
}
.all-projects {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
.item {
    width: 50%;
    margin-bottom: 72px;
    text-align: center;
}
.item a {
    font-weight: 400;
    color: var(--main-black);
}
.item h1 {
    margin-top: 32px;
}

/* -----project pages----*/
.project {
    width: 60%;
    margin: 0 auto;
}
.project img {
    width: 100%;
}
.project-title {
    font-size: 40px;
    text-align: center;
}
.bannerContainer img {
    width: 100%;
}
.prologue {
    font-style: italic;
}
.quote {
    font-size: 28px;
    line-height: 44px;
    width: 90%;
    margin: 32px auto 0 auto;
    color: var(--darker-blue);
    font-style: italic;
}
h1, h2, h3, h4 {
    margin-top: 48px;
    font-weight: 700;
}
ul li {
    padding-top: 8px;
    font-size: 18px;
}
.project p, ol li, ul li {
    font-size: 18px;
    line-height: 32px;
}
.column {
    float: left;
    width: 33%;
}
.column-2 {
    float: left;
    width: 66%;
}
.column-center {
    float: left;
    width: 31%;
}
.row:after {
    content: "";
    display: table;
    clear: both;
}
.stat {
    color: var(--darker-blue);
    font-weight: 600;
}
img.infographic {
    width: 60%;
}
img.shadow {
    box-shadow: 3px 3px 6px 1px var(--light-outline);
}
.competition img {
    height: 60px;
    width: auto;
    padding-right: 40px;
}
.mockup img {
    display: inline-block;
    width: 48%;
    box-shadow: 3px 3px 6px 1px var(--light-outline);
}
img.left {
    margin-right: 4%;
}
img.center-image {
    display: block;
    width: 75%;
    margin: 0 auto;
}
img.reduce-size {
    width: 50%;
}
.image-row {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}
.image-row img {
    height: 176px;
    width: auto;
    box-shadow: 1px 2px 6px 1px var(--light-outline);
}
.wireframe .image-row img {
    height: 392px;
}
.overflow {
    width: 90vw;
    position: relative;
    left: calc(-45vw + 50%);
}
.design img {
    float: left;
    width: 48%;
    margin-right: 4%;
    box-shadow: 1px 2px 6px 1px var(--light-outline);
}
.center-text {
    text-align: center;
}


/* -----footer----*/
.footer {
    clear: both;
    margin-bottom: 16px;
    margin-top: 40px;
    text-align: center;
}
.footer img {
    width: 14px;
}

/* -----tables----*/
table {
    margin-top: 16px;
    margin-bottom: 16px;
    border-collapse: collapse;
    border: 1px solid var(--main-blue);
    width: 100%;
}
td {
    border: 1px solid var(--main-blue);
    padding: 16px;
    text-align: left;
}​
th { /* for some reason this is neccessary to not mess up table formatting */
}
th {
    padding: 8px 16px;
    text-align: left;
    background-color: var(--main-blue);
    color: var(--main-black);
}
td div {
    margin-top: 8px;
}


